import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {},

  mutations: {
    menuAdd(state, value) {
      state.menuList = value
    },
  },
  actions: {
    menuAdd(mutations, value) {
      mutations.commit('menuAdd', value)
    },
  },
  modules: {},
})
